.amazing-offer{
  padding-top: 162px;
  padding-bottom: 152px;
    &__title{
      margin-bottom: 100px;
    }
    &__slider{
      margin-bottom: 88px;
    }
  &__pagination{
    text-align: center;
    .swiper-pagination-bullet{
      width: 20px;
      height: 20px;
      background: #fff;
      opacity: 1;
      &:not(:last-child){
        margin-right: 10px;
      }
      &.swiper-pagination-bullet-active{
        background: var(--main);
      }
    }
  }
}
.amazing-item{
  position: relative;
  transform: scale(0.9);
  transition: all .3s;
    &__about{
      position: absolute;
      left: 25px;
      bottom: 10px;
    }
    &__name{
      font-size: 24px;
    }
    &__price{
      font-family: var(--Poppins);
      font-size: 36px;
      font-weight: 600;
    }
    &.swiper-slide-active{
      transform: scale(1);
    }
}